<template>
	<view class="personInpt">
		<comp-input :xing="true" :firstName="start" :password='false' @myblur="myblurnation0" placer="统一社会信用代码"></comp-input>
		<comp-input :xing="true" :firstName="start" :password='false' @myblur="myblurnation1" placer="企业名称">
		</comp-input>
		<comp-input :xing="true" :firstName="start" :password='false' @myblur="myblurnation2" placer="法定代表人姓名">
		</comp-input>
		<comp-input :xing="true" :firstName="start" :password='false' @myblur="myblurnation3" placer="法定代表人联系方式">
		</comp-input>
       <comp-input :xing="true" :firstName="start" :password='false' @myblur="myblurnation4" placer="企业住所地">
		</comp-input>
    <comp-input :password='false' @myblur="myblurnation5" placer="法定代表人邮箱"></comp-input>
		 


      <view class="loginButtom">
      	<button type="primary" @click="personalData" >登录</button>
      </view>
	</view>
</template>

<script>
	import {
		start
	} from "@/static/images/lxg/base64.js"; 
	import compInput from "../compInput/index.vue" 
	import { orgupdate  } from "@/api/index.js";
	export default {
		components: {
			compInput 
			 
		},
		props: [],
	 
		data() {
			return {
				disabled:true,
				sexList: [],
				visible: false,
				// creditCode: "2"
				// legalRepresentative: "4"
				// legalRepresentativeEmail: "7"
				// mobilePhone: "5"
				// orgName: "3"
				// orgType: 1
				// residence: "6"
				start: start,
				perData: {
					creditCode:"",
					legalRepresentative: "",
					legalRepresentativeEmail: "",
					mobilePhone: "",
					orgName: "",
					orgType: 1,
					residence: "" 
				}, 
				index: null,
				ssss: false,
				classIndex: "",
				nationIndex: "",
				getdictName: '',
				nationName:""


			};
		},
		onShow(options) {

		},
		methods: {
			 
			postPersonal(){
				
				 
				let _this = this
				let getPer =this.perData
				let params={
					creditCode:getPer.creditCode,
					legalRepresentative: getPer.legalRepresentative,
					legalRepresentativeEmail:  getPer.legalRepresentativeEmail,
					mobilePhone:  getPer.mobilePhone,
					orgName:  getPer.orgName,
					orgType:  getPer.orgType, 
					residence: getPer.residence,
				}
				console.log("提交的参数",params)
				orgupdate( params ).then(res => {
					console.log("ssssssss=====>",res)
					if(res.code==200){
						_this.$emit("okpersonaldata")
						 
						 
					}
					 
				
				})
			},
            personalData(){
			 // creditCode: "111111111"
			 // legalRepresentative: "333333333"
			 // legalRepresentativeEmail: "666666@qq.com"
			 // mobilePhone: "44444444"
			 // orgName: "222222222"
			 // orgType: 1
			 // residence: "5555555"
				let params = this.perData
				if(params.creditCode == ""){
					uni.showToast({
						icon:'none',
						title:"请填写统一社会信用代码"
					})
					return
				}
				if(params.orgName == ""){
				uni.showToast({
					icon:'none',
					title:"请填写企业名称"
				})
					return
				}
				if(params.legalRepresentative == ""){
					uni.showToast({
						icon:'none',
						title:"请填写法定代表人"
					})
					return
				}
				 
				if(params.mobilePhone == ""){
					uni.showToast({
						title:"请填写企业住所"
					})
					return
				}
				 
				 this.postPersonal()
				console.log("99999999999")
			},
			nationListChange(e){
				var thisIndex = e.detail.value;
				this.nationName = this.nationList[thisIndex].dictName;
				this.perData.nation = this.nationList[thisIndex].dictCode 
				this.nationIndex = thisIndex;
				 
			},
			classListPChange(e) {
				var thisIndex = e.detail.value;
				this.perData.userSex = this.sexList[thisIndex].dictName;
				this.perData.userSexCode = this.sexList[thisIndex].dictCode
				 
				this.classIndex = thisIndex;

				 
			},
			bindPickerChange: function(e) {
				console.log('picker发送选择改变，携带值为', e.target.value)
				// this.index = e.target.value
			},

			showTime() {
				this.visible = true
				this.getpopupPicker()
			},
			okPicker(v) {
				this.perData.birthdayStr = v
				this.visible = false
				this.getclosePicker()
			},
			closePicker() {

				this.getclosePicker()
			},
			getpopupPicker() {
				this.$refs.popupPicker.open()
			},
			getclosePicker() {
				this.$refs.popupPicker.close()
			},
			myblurnation0(messarr) {
				this.perData.creditCode = messarr[0]

			},
 
			myblurnation1(messarr) {
				this.perData.orgName = messarr[0]

			},
			myblurnation2(messarr) {
				this.perData.legalRepresentative = messarr[0]

			},
			myblurnation3(messarr) {
				this.perData.mobilePhone = messarr[0]

			},
			myblurnation4(messarr) {
				this.perData.residence = messarr[0]
			
			},
			myblurnation5(messarr) {
				this.perData.legalRepresentativeEmail = messarr[0]
			
			},
			 
			noInputData(){
				
					let params = this.perData
					if(params.nation != ""&&params.idCard != ""&&params.address != ""&&params.email != ""
					&&params.userSex != ""&&params.birthdayStr != ""&&params.userName != ""
					){ 	this.disabled =false
					}
				 
			}
		},

	};
</script>

<style scoped>
	.personInpt {
		padding: 0px 40upx;
		padding-bottom: 200upx;
	}

	.displayRow {
		color: #8b949f;
		font-size: 14px;
	}

	.pickerMargin {
		margin: 40upx 0;
	}

	.displablock {
		display: block;
		font-size: 16px;
		line-height: 1.4em;
		height: 1.4em;
		min-height: 1.4em;
		overflow: hidden;
	}

	.pickerAll {
		height: 80upx;
		border: 1px solid #CCCCCC;
		border-radius: 10upx;
	}

	.pickerstart {
		width: 16upx;
		height: 16upx;
		margin-right: 20upx;
		margin-left: 20upx;
	}
	.loginButtom {
		margin-top: 40px;
		 
		
	}
	
	.loginButtom button {
		border-radius: 100upx;
		 
	}
	.birthdayStyle{
		color: #333;
	}
</style>
